import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActionDialogComponent } from '../action-dialog/action-dialog.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { MatTableFilter } from 'mat-table-filter';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { Observable } from 'rxjs';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import * as XLSX from 'xlsx';
import { ViewDialogComponent } from '../view-dialog/view-dialog.component';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import { map, startWith } from 'rxjs/operators';
import { Validators, FormBuilder, FormGroup, AbstractControl, ValidatorFn } from '@angular/forms';
import { EVChargingStationRequestService } from 'app/shared/services/customer-management/evCharging-station-request.service';
import { CountriesService } from 'app/shared/services/countries.service';


function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}

export class ApprovEvChargingStationRequest {
  id: number;
  user_id: string;
  user_name: string;
  mobile: string;
  email: string;
  volume_of_ev_user: number;
  space_for_station: number;
  population_density: number;
  lat: number;
  lng: number;
  address1: string;
  address2: string;
  PIN: number;
  landmark: string;
  city_name: string;
  state_id: number;
  state_name: string;
  country_id: number;
  country_name: string;
  remarks: string;
  like_count: number;
  dislike_count: number;
  status: string;
  created_date: string;
  createdby: number;
  modify_date: string;
  modifyby: number;
  statusString: string;
}

@Component({
  selector: 'app-approved-list',
  templateUrl: './approved-list.component.html',
  styleUrls: ['./approved-list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})

export class ApprovedListComponent implements OnInit {

  filterEntity: ApprovEvChargingStationRequest;
  filterType: MatTableFilter;
  displayedColumns: string[] = ['user_name', 'mobile', 'email', 'volume_of_ev_user', 'space_for_station', 'city_name', 'state_name', 'status', 'action'];
  head = [['UserName', 'Mobile', 'Email', 'Like', 'Dislike', 'City', 'State', 'Status']];
  dataSource: MatTableDataSource<ApprovEvChargingStationRequest>;
  localStorage: any;
  allChargers: any;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  dialogRef: any;
  usernameSearch = false;
  mobileSearch = false;
  emailSearch = false;
  evVolSearch = false;
  citySearch = false;
  stateSearch = false;
  statusSearch = false;
  staSpaceSearch = false
  exportedData: any[];
  form: FormGroup;
  approvedCharReq: any;
  filteredOptionCountry: Observable<any[]>;
  filteredOptionState: Observable<any[]>;
  filteredOptionCity: Observable<any[]>;
  countries: any;
  states: any;
  stateName: any;
  cities: any;
  country_id: any;
  state_id: any;
  cityId: any;
  likeFlag: boolean = false;
  dislikeFlag: boolean = false;

  constructor(private evCharStationReqService: EVChargingStationRequestService, public dialog: MatDialog, private fb: FormBuilder,
    private snackBar: MatSnackBar, private cdr: ChangeDetectorRef, public countryService: CountriesService) {

  }

  ngOnInit() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.form = this.fb.group({
      city: ['', [autocompleteObjectValidator(), Validators.required]],
      state: ['', [autocompleteObjectValidator(), Validators.required]],
      country: ['', [autocompleteObjectValidator(), Validators.required]],
    });
    this.filterEntity = new ApprovEvChargingStationRequest();
    this.filterType = MatTableFilter.ANYWHERE;

    this.getAllCountry();
    this.getAllState();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getAllCountry() {
    this.countryService.getAllCountry().subscribe(
      (resp: any) => {
        this.countries = resp;
        this.filteredOptionCountry = this.form.controls.country.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterCountry(name) : this.countries)
          );
      }, (err) => {

      }
    )
  }
  displayCountry(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCountry(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.countries.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  getAllState() {
    this.countryService.getAllState().subscribe(
      (resp: any) => {
        this.states = resp;
        this.filteredOptionState = this.form.controls.state.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterState(name) : this.states)
          );
      }, (err) => {

      }
    )
  }
  displayState(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterState(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.states.filter(option => option.name.toLowerCase().includes(filterValue));
  }

  getAllCity() {
    this.countryService.getAllCities().subscribe(
      (resp: any) => {
        this.cities = resp;
        this.filteredOptionCity = this.form.controls.city.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterCity(name) : this.cities)
          );
      }, (err) => {

      }
    )
  }
  displayCity(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCity(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.cities.filter(option => option.name.toLowerCase().includes(filterValue));
  }

  onCountryChange(event) {
    this.countries = [];
    this.getAllCountry();
    this.getStatesByCountry(event);
    //this.form.get('pincode').setValue( "");
    this.form.get('state').setValue("");
    this.form.get('city').setValue("");
  }
  getStatesByCountry(state) {
    this.state_id = state.id;
    this.countryService.getStateByCountry(this.state_id).subscribe(
      (statedata: any) => {
        //this.form.reset();
        this.states = statedata;
        // const toSelect = this.states.find(c => c.id == data[0].state_id);
        // this.form.get('state').setValue(toSelect);
        this.filteredOptionState = this.form.controls.state.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterState(name) : this.states)
          );

      }, (err) => {

      }
    )
  }
  getCitiesByStates(stateId) {
    this.state_id = stateId.id;
    this.countryService.getCityByState(this.state_id).subscribe(
      (citiesdata: any) => {
        this.cities = citiesdata;
        this.filteredOptionCity = this.form.controls.city.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterCity(name) : this.cities)
          );

      }, (err) => {

      }
    )
  }
  getCountryByState(stateId) {
    this.state_id = stateId.id;
    this.countryService.getCountryByState(this.state_id).subscribe(
      (countrydata: any) => {
        //this.form.reset();
        this.countries = countrydata;
        const toSelect = this.countries.find(c => c.id == countrydata[0].id);
        this.form.get('country').setValue(toSelect);

        this.filteredOptionCountry = this.form.controls.country.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterCountry(name) : this.countries)
          );

      }, (err) => {

      }
    )
  }
  onStateChange(event) {
    this.getCitiesByStates(event);
    this.getCountryByState(event);
    this.form.get('city').setValue("");
    //this.form.get('pincode').setValue("");
  }
  onCityChange(event) {
    this.cityId = event.id;
    this.getApprovedRejectCharStatReq(event.id);
  }

  getApprovedRejectCharStatReq(cityId) {
    if (this.localStorage.role_name == "END USER") {
      this.evCharStationReqService.getApproveRejectByUserIdCityId(this.localStorage.userId, cityId).subscribe(
        (resp: any) => {
          if (resp.status == true) {
            this.approvedCharReq = resp.data;
            for (var i = 0; i < this.approvedCharReq.length; i++) {
              if (this.approvedCharReq[i].status == 'A') {
                this.approvedCharReq[i].statusString = 'Approved';
              } else {
                this.approvedCharReq[i].statusString = 'Reject';
              }
            }
            this.dataSource = new MatTableDataSource(this.approvedCharReq);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            this.cdr.detectChanges();
          } else {
            this.dataSource = new MatTableDataSource();
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            this.cdr.detectChanges();
            this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 3000 })
            return;
          }

        }, (err) => {
        }
      );
    } else {
      this.evCharStationReqService.getApproveRejectEvChargingStationRequestByCityId(this.localStorage.userId, cityId).subscribe(
        (resp: any) => {
          if (resp.status == true) {
            this.approvedCharReq = resp.data;
            for (var i = 0; i < this.approvedCharReq.length; i++) {
              if (this.approvedCharReq[i].status == 'A') {
                this.approvedCharReq[i].statusString = 'Approved';
              } else {
                this.approvedCharReq[i].statusString = 'Reject';
              }
            }
            this.dataSource = new MatTableDataSource(this.approvedCharReq);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            this.cdr.detectChanges();

          } else {
            this.dataSource = new MatTableDataSource([]);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            this.cdr.detectChanges();
            this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 3000 })
            return;
          }

        }, (err) => {
        }
      );
    }

  }
  viewDialog(action, data, buttonAction): void {
    let params = {
      action: action,
      data: data,
      buttonAction: buttonAction
    }
    const dialogRef = this.dialog.open(ViewDialogComponent, {
      width: '1200px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      this.getApprovedRejectCharStatReq(this.cityId);
    });
  }

  changeVisibility(visibleOption, ev) {
    ev.stopPropagation();
    if (visibleOption == 'usernameSearch') {
      this.usernameSearch = !this.usernameSearch;
      this.filterEntity.user_name = "";
    }
    if (visibleOption == 'emailSearch') {
      this.emailSearch = !this.emailSearch;
      this.filterEntity.email = "";
    }
    if (visibleOption == 'mobileSearch') {
      this.mobileSearch = !this.mobileSearch;
      this.filterEntity.mobile = "";
    }
    if (visibleOption == 'evVolSearch') {
      this.evVolSearch = !this.evVolSearch;
      this.filterEntity.volume_of_ev_user = null;
    }
    if (visibleOption == 'staSpaceSearch') {
      this.staSpaceSearch = !this.staSpaceSearch;
      this.filterEntity.space_for_station = null;
    }
    if (visibleOption == 'citySearch') {
      this.citySearch = !this.citySearch;
      this.filterEntity.city_name = " ";
    }
    if (visibleOption == 'stateSearch') {
      this.stateSearch = !this.stateSearch;
      this.filterEntity.state_name = "";
    }
    if (visibleOption == 'statusSearch') {
      this.statusSearch = !this.statusSearch;
      this.filterEntity.statusString = " ";
    }
  }

  tableExport() {
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var evDealers = this.dataSource.filteredData[i];
      var data = {
        'UserName': evDealers.user_name, 'Mobile': evDealers.mobile, 'Email': evDealers.email,
        'Like': evDealers.like_count, 'Dislike': evDealers.dislike_count, 'City': evDealers.city_name, 'State': evDealers.state_name,
        'Status': evDealers.statusString
      };
      this.exportedData.push(data);
    }
    const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();

    XLSX.utils.book_append_sheet(workBook, workSheet, 'Approved/Reject EV Charging Station Request List');
    XLSX.writeFile(workBook, 'Approved/Reject EV Charging Station Request List.xlsx');
  }

  pdfExport() {
    var doc = new jsPDF({ // set needed dimensions for any elemen

    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var evDealers = this.dataSource.filteredData[i];

      var data = [evDealers.user_name, evDealers.mobile, evDealers.email, evDealers.like_count, evDealers.dislike_count, evDealers.city_name,
      evDealers.state_name, evDealers.statusString];

      this.exportedData.push(data);
    }
    var img = new Image();
    var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
    imgRight.src = srcSparkEV;
    var createdDate = new Date().toLocaleString(('en-GB'));
    img.src = src;
    doc.setFontSize(11);
    doc.setTextColor(100);
    doc.setFontSize(18);

    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla', fillColor: [11, 148, 68] },
      styles: {
        cellPadding: 1,
        fontSize: 8,

      },

      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage: data => {
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);

        doc.text('Approved/Reject EV Charging Station Request List Report', doc.internal.pageSize.getWidth() / 2, 11, { align: 'center' });

        doc.setTextColor(211, 211, 211);
        doc.setFontSize(60);
        doc.setGState(doc.GState({ opacity: 0.6 }));
        doc.text('Confidential/Not for Public use', 25, 285, null, 60,);
        doc.setGState(doc.GState({ opacity: 1 }));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:' + createdDate, 15, 18);

        doc.text('Generated By:' + this.localStorage.f_Name, 195, 18, { align: 'right' });
        doc = this.addWaterMark(doc);
      }
    })

    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow');
    // below line for Download PDF document  
    doc.save('Approved/Reject EV Charging Station Request List report.pdf');

  }
  addWaterMark(doc) {
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
    doc.setFontSize(10);
    //Print Page 1 of 4 for example

    doc.text('Page ' + String(i) + ' of ' + String(newpage), 210 - 20, 297 - 10, null, null, "right");
    return doc;
  }

  onLike(element) {
    
    const params: any = {
      id: element.id,
      user_id: this.localStorage.userId,
      like_dislike: 1,
      status: "Y",
      created_by: this.localStorage.userId,
      modify_by: this.localStorage.userId,
    }
    this.evCharStationReqService.onLikeDislikeRequest(params).subscribe(
      (resp: any) => {
        if (resp.status == true) {
          if(element.like_count == 0){
            this.snackBar.open(MessageConstant.like, 'Dismiss', { duration: 5000 });
          }else{
            this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
          }
          
        } else {
          this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
        }
      }, (err) => {
        this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
      }
    );

  }
  onDislike(element) {

    const params: any = {
      id: element.id,
      user_id: this.localStorage.userId,
      like_dislike: -1,
      status: "Y",
      created_by: this.localStorage.userId,
      modify_by: this.localStorage.userId,
    }
    this.evCharStationReqService.onLikeDislikeRequest(params).subscribe(
      (resp: any) => {
        if (resp.status == true) {
          if(element.dislike_count == 0){
            this.snackBar.open(MessageConstant.dislike, 'Dismiss', { duration: 5000 });
          }else{
            this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
          }
          
        } else {
          this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
        }
      }, (err) => {
        this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
      }
    );

  }



}

