import { Routes } from '@angular/router';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
import { CreateComponent } from '../create/create.component';
import { ApprovedListComponent } from '../approved-list/approved-list.component';
import { ListComponent } from '../list/list.component';
import { MyPendingListComponent } from '../myPending-list/myPending-list.component';


export const TABS_DEMO_ROUTES: Routes = [
  {
     path : 'create', component : CreateComponent,canActivate:[AuthGuard] 
  },
  { 
    path : 'listEVStationPendingRequests', component : ListComponent , canActivate:[AuthGuard] 
  },
  { 
    path : 'listEVStationRequests', component : ApprovedListComponent , canActivate:[AuthGuard] 
  },
  { 
    path : 'listEVStationMyPendingRequests', component : MyPendingListComponent , canActivate:[AuthGuard] 
  },
  { 
    path: '', redirectTo: 'listEVStationRequests', pathMatch: 'full' 
  },
];
